"use client";
import axios from "axios";
import { useEffect, useState } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { format } from "date-fns";
import Header from "@/app/components/header";
import AirPollution from "@/app/components/widget/airPollution"

interface Weather {
  city: string;
  current: {
    temperature: number;
    feels_like: number;
    humidity: number;
    pressure: number;
    wind_speed: number;
    visibility: number;
    description: string;
    icon: string;
  };
  forecast: [
    {
      date: string;
      temperature: {
        min: number;
        max: number;
      };
      humidity: number;
      icon: string;
      description: string;
    }
  ];
}

export default function Dashboard() {
  const [city, setCity] = useState("Boac");
  const [weather, setWeather] = useState<Weather | null>(null);
  const [loading, setLoading] = useState(false);
  const [darkMode, setDarkMode] = useState(false);

  const getWeather = async (cityName: string) => {
    setLoading(true);
    try {
      const response = await axios.get(
        `http://localhost:5000/get-weather?city=${cityName}`
      );
      console.log("Weather data:", response.data);
      setWeather(response.data);
    } catch (error) {
      console.error("Error fetching weather data:", error);
      setWeather(null);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getWeather(city);
  }, [city]);

  const getBackgroundColor = (description: string | undefined) => {
    if (!description) return "bg-gray-200";

    const lowerDesc = description.toLowerCase();
    if (lowerDesc.includes("rain") || lowerDesc.includes("drizzle"))
      return "bg-blue-500";
    if (lowerDesc.includes("cloud") || lowerDesc.includes("overcast"))
      return "bg-gray-500";
    if (lowerDesc.includes("clear") || lowerDesc.includes("sun"))
      return "bg-yellow-500";
    if (lowerDesc.includes("snow") || lowerDesc.includes("sleet"))
      return "bg-white";

    return "bg-gray-200";
  };

  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
  };

  return (
    <div
      className={`min-h-screen flex flex-col py-10 px-10 transition-all duration-500 ${
        darkMode ? "bg-gray-900 text-white" : "bg-white text-black"
      }`}
      style={{
        backgroundImage: `url('/path/to/background-image.jpg')`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
      }}
    >
       <header className="text-4xl font-bold mb-8">
     <Header darkMode={darkMode} toggleDarkMode={toggleDarkMode}/>
    </header>
      <div className="flex w-full max-w-6xl">
        <div className="w-1/4 p-4">
          <div
            className="bg-white bg-opacity-20 backdrop-filter backdrop-blur-lg rounded-lg p-6 shadow-lg"
            style={{
              backgroundImage: `url('/path/to/container-background.jpg')`,
              backgroundSize: 'cover',
              backgroundPosition: 'center',
            }}
          >
            <div className="flex flex-col items-center">
              <Input
                value={city}
                onChange={(e) => setCity(e.target.value)}
                placeholder="Enter city name"
                className="mb-4"
              />
              <Button onClick={() => getWeather(city)}>Search</Button>
            </div>
          </div>
        </div>
        <div className="w-3/4 p-4">
          {loading ? (
            <p>Loading...</p>
          ) : weather ? (
            <div className="grid grid-cols-1 gap-4">
              <div
                className={`p-6 rounded-lg shadow-lg ${getBackgroundColor(
                  weather.current.description
                )}`}
                style={{
                  backgroundImage: `url('/path/to/container-background.jpg')`,
                  backgroundSize: 'cover',
                  backgroundPosition: 'center',
                }}
              >
                <div className="flex items-center">
                  <img
                    src={weather.current.icon}
                    alt="Weather icon"
                    className="w-20 h-20 mr-4"
                  />
                  <div>
                    <h2 className="text-2xl font-bold drop-shadow-lg">
                      {weather.city}
                    </h2>
                    <p className="text-xl drop-shadow-lg">
                      {weather.current.temperature}°C
                    </p>
                    <p className="drop-shadow-lg">
                      Humidity: {weather.current.humidity}%
                    </p>
                    <p className="drop-shadow-lg">
                      Wind Speed: {weather.current.wind_speed} m/s
                    </p>
                    <p className="drop-shadow-lg">
                      Description: {weather.current.description}
                    </p>
                  </div>
                </div>
              </div>
              <div className="grid grid-cols-3 gap-4">
                {weather.forecast.map((forecast, index) => (
                  <div
                    className={`p-4 rounded-lg shadow-lg ${getBackgroundColor(
                      forecast.description
                    )}`}
                    key={index}
                    style={{
                      backgroundImage: `url('/path/to/container-background.jpg')`,
                      backgroundSize: 'cover',
                      backgroundPosition: 'center',
                    }}
                  >
                    <p className="font-bold drop-shadow-lg">
                      {format(new Date(forecast.date), "EEEE")}
                    </p>
                    <img
                      src={forecast.icon}
                      alt="Forecast icon"
                      className="w-12 h-12 mb-2"
                    />
                    <p className="drop-shadow-lg">
                      Min: {forecast.temperature.min}°C
                    </p>
                    <p className="drop-shadow-lg">
                      Max: {forecast.temperature.max}°C
                    </p>
                    <p className="drop-shadow-lg">
                      Humidity: {forecast.humidity}%
                    </p>
                  </div>
                ))}
              </div>
            </div>
          ) : (
            <p className="text-red-500"></p>
          )}
        </div>
      </div>
    </div>
  );
}
